import React from 'react';

const ContactList = ({ findContact, deleteContact }) => {
  return (
    <ul>
      {findContact().map(contact => (
        <li key={contact.id}>
          <p>
            {contact.name}: {contact.number}
          </p>
          <button type="button" id={contact.id} onClick={deleteContact}>
            Delete
          </button>
        </li>
      ))}
    </ul>
  );
};

export default ContactList;
